@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

    *{
        padding: 0px;
        margin: 0px;
        box-sizing: border-box;
        font-family: 'Poppins', sans-serif;
    }

    :root{
        --linear-grad: linear-gradient(to right, #98daf8, #0094d4);
        --grad-clr1: #98daf8;
        --grad-clr2: #0094d4;
    }

    body{
        height: 100vh;
        background: #f6f5f7;
        display: grid;
        place-content: center;
    }

    .container{
        position: relative;
        width: 850px;
        height: 500px;
        background-color: #fff;
        box-shadow: 25px 30px 55px #5557;
        border-radius: 13px;
        overflow: hidden;
        margin: auto;

    }

    .form-container{
        position: absolute;
        width: 60%;
        height: 100%;
        padding: 0px 40px;
        transition: all 0.6s ease-in-out;
    }

    .sign-up-container{
        opacity: 0;
        z-index: 1;
    }

    .sign-up-container{
        z-index: 2;
    }

    form{
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 0px 50px;
    }

    .infield{
        position: relative;
        margin: 8px 0px;
        width: 100%;
    }

    input{
        width: 100%;
        padding: 12px 8px;
        background-color: #f3f3f3;
        border: none;
        outline: none;
    }

    label{
        position: absolute;
        left: 50%;
        top: 100%;
        transform: translateX(-50%);
        width: 0%;
        height: 2px;
        background: var(--linear-grad);
        transition: 0.3s;
    }

    input:focus ~ label{
        width: 100%;
    }

    a{
        color: #333;
        font-size: 14px;
        text-decoration: none;
        margin: 15px 0px;
    }

    a.forgot{
        padding-bottom: 3px;
        border-bottom: 2px solid #eee;
    }

    button{
        border-radius: 20px;
        border: none;
        background: var(--grad-clr2);
        color: #fff;
        font-size: 12px;
        font-weight: bold;
        padding: 12px 45px;
        letter-spacing: 1px;
        text-transform: uppercase;
    }

    .form-container button{
        margin-top: 17px;
        transition: 80ms ease-in;
    }

    .overlay-container{
        position: absolute;
        top: 0;
        left: 60%;
        width: 40%;
        height: 100%;
        overflow: hidden;
        transition: transform 0.6s ease-in-out;
        z-index: 9;
    }

    #overlayBtn{
        cursor: pointer;
        position: absolute;
        left: 50%;
        top: 304px;
        transform: translateX(-50%);
        width: 143.67px;
        height: 40px;
        border: 1px solid #fff;
        background: transparent;
        border-radius: 20px;
    }

    .overlay{
        position: relative;
        background: var(--linear-grad);
        color: #fff;
        left: -150%;
        height: 100%;
        width: 250%;
        transition: transform 0.6s ease-in-out;
    }

    .overlay-panel{
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        padding: 0px 40px;
        text-align: center;
        height: 100%;
        width: 340px;
        transition: 0.6s ease-in-out;
    }

    .overlay-right{
        right: 0%;
        transform: translateX(0%);
    }

    .right-panel-active .overlay-right{
        transform: translateX(35%);
    }

    p{
        font-size: 14px;
        font-weight: 300;
        line-height: 20px;
        letter-spacing: 0.5px;
        margin: 25px 0px 35px;
    }